<template>
  <div class="video-container background-container">
    <div class="header">
      <el-page-header @back="goBack" title="" content="企业介绍">
      </el-page-header>
      <header-menu class="header-menu"></header-menu>
    </div>

    <div class="video-wrapper_bg">
      <div class="video-wrapper">
        <!-- poster 视频图片 ;source 视频 -->
        <video ref="video" controls @play="onVideoPlay" @pause="onVideoPause"  poster="@/assets/img/aid.jpg">
          <source src="@/assets/companyVideo.mp4" type="video/mp4" />
        </video>

        <button class="paly" @click="toggleVideo" v-if="!isPlaying">
          <img src="@/assets/img/paly.png" alt="" />
        </button>
      </div>
    </div>
  </div>
</template>
  
  <script>
import header from "@/views/header.vue";
export default {
  components: {
    "header-menu": header,
  },
  data() {
    return {
      isPlaying: false,
    };
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },

    toggleVideo() {
      const videoElement = this.$refs.video;
      if (this.isPlaying) {
        videoElement.pause();
      } else {
        videoElement.play();
      }
      this.isPlaying = !this.isPlaying;
    },
    onVideoPlay() {
      this.isPlaying = true;
    },
    onVideoPause() {
      this.isPlaying = false;
    },

    goToHomePage() {
      // 在这里添加返回首页的导航逻辑
      console.log("Returning to the home page");
    },
  },
};
</script>
  
  <style scoped>
@import url(../../src/assets/css/base.css);
.background-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.video-wrapper_bg {
  /* // 替换背景图片 */
  background: url(../assets/img/banner2.jpg) no-repeat;
  background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
}
.video-wrapper {
  position: relative;
  margin: 20px 10px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: #434343 0 0 8px 1px;
  background: #000;
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  cursor: pointer;
}
.paly {
  background-color: rgb(255 255 255 / 67%);
  color: white;
  border: none;
  cursor: pointer;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px;
}
.paly img {
  display: block;
  width: 100%;
}

video {
  max-width: 100%;
}
</style>
  